import moment from 'moment'
import { GET_STOCK_TAKE_ACTION, CREATE_JOB_SHEET, SET_AREA } from 'apiServices/action'
import { getPoBySkuId, removeEntryModule } from 'apiServices'
import { getOutboundType } from 'apiServices/dropdown'
import { isFloat } from 'services'
import {totalListItemWeightCount} from 'Page/StockEntry/GeneralServices/moduleServices'

// update sku list tobe selected
export const selectSku = async ({
    idx,
    checked,
    dispatch,
    setLocalEntryData,
    moduleName,
    moduleSummary,
    moduleEntry,
    warehouseUuid,
    areaId }) => {
    let moduleSummaryNew = [...moduleSummary]
    moduleSummaryNew[idx].checked = checked
    dispatch({ type: GET_STOCK_TAKE_ACTION, [`${moduleName}summaryData`]: moduleSummaryNew })

    const selectedData = moduleSummaryNew?.filter(ldata => {
        if (ldata.checked) return ldata
    })
    let jsSelector = []
    if (moduleEntry) {
        moduleEntry.jobPost.list.map(data => {
            const jsList = (({ skuUuid }) => ({ skuUuid }))(data)
            if (jsList.skuUuid) {
                jsSelector.push(jsList.skuUuid)
            }
        })
    }
    let jsD = []
    const a = selectedData.map(async (list, sI) => {
        const isInclude = jsSelector.includes(list.skuUuid)
        if (!isInclude) {
            const outboundType = await getOutboundType({})
            list['outboundTypeList'] = outboundType
            const data = await getPoBySkuId({ skuId: list.skuUuid, warehouseUuid, dispatch, areaId: areaId.value, moduleName })
            if (data) {
                const addPoItem = data.map(poList => {
                    poList.itemList = []

                    const expiryMs = poList.expiryDate ? moment(poList.expiryDate).valueOf() : null
                    const inBoundyMs = poList.inboundDate ? moment(poList.inboundDate).valueOf() : null
                    poList.expiryDate = expiryMs
                    poList.inboundDate = inBoundyMs
                    poList.skuUuid = list.skuUuid
                    poList.uom = list.unitOfMeassures
                    poList.category = list.category
                    poList.initialWeight = Number.isInteger(poList.initialWeight) ? parseInt(poList.initialWeight) : parseFloat(poList.initialWeight)
                    if (areaId?.label?.toLowerCase() !== 'staging') poList.totalPack = null
                    return poList
                })
                list['poList'] = addPoItem
                jsD.push(list)
            }

        }
    })
    const aF = await Promise.all(a)
    let newJs = []
    if (moduleEntry) {
        newJs = moduleEntry.jobPost.list.concat(jsD)
    }
    if (!moduleEntry) newJs = jsD

    const uncheckedSku = moduleSummaryNew[idx]
    if (!uncheckedSku.checked) {
        newJs = newJs.filter(data => { return data.skuUuid !== uncheckedSku.skuUuid })
    }

    const id = moduleEntry?.jobPost?.id
    const jobSheet = {
        jobPost: {
            id: id ? id : 0,
            warehouseSource: warehouseUuid,
            warehouseDestination: warehouseUuid,
            areaSource: parseInt(areaId?.value),
            areaName: areaId?.label,
            areaDestination: parseInt(areaId?.value),
            list: newJs
        }
    }
    if (newJs.length === 0) dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: null })
    setLocalEntryData(jobSheet)
    // dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: jobSheet })
}

//pre Add SKU
export const preAddSku = async ({
    idx,
    checked,
    dispatch,
    moduleName,
    moduleSummary,
    moduleEntry,
    warehouseUuid,
    areaId,
    preAddHandler }) => {
    let moduleSummaryNew = [...moduleSummary]
    moduleSummaryNew[idx].checked = checked

    const selectedData = moduleSummaryNew?.filter(ldata => {
        if (ldata.checked) return ldata
    })
    let jsSelector = []
    if (moduleEntry) {
        moduleEntry.map(data => {
            const jsList = (({ skuUuid }) => ({ skuUuid }))(data)
            if (jsList.skuUuid) {
                jsSelector.push(jsList.skuUuid)
            }
        })
    }
    let jsD = []
    const a = selectedData.map(async (list, sI) => {
        const isInclude = jsSelector.includes(list.skuUuid)
        if (!isInclude) {
            const outboundType = await getOutboundType({})
            list['outboundTypeList'] = outboundType
            const data = await getPoBySkuId({ skuId: list.skuUuid, warehouseUuid, dispatch, areaId: areaId.value, moduleName })
            if (data) {
                const addPoItem = data.map(poList => {
                    poList.itemList = []

                    const expiryMs = poList.expiryDate ? moment(poList.expiryDate).valueOf() : null
                    const inBoundyMs = poList.inboundDate ? moment(poList.inboundDate).valueOf() : null
                    poList.expiryDate = expiryMs
                    poList.inboundDate = inBoundyMs
                    poList.skuUuid = list.skuUuid
                    poList.uom = list.unitOfMeassures
                    poList.category = list.category
                    poList.initialWeight = isFloat(poList.initialWeight) ? isFloat(poList.initialWeight) : poList.initialWeight
                    if (areaId?.label?.toLowerCase() !== 'staging') poList.totalPack = null
                    return poList
                })
                list['poList'] = addPoItem
                jsD.push(list)
            }

        }
    })
    const aF = await Promise.all(a)
    let newJs = []
    if (moduleEntry) {
        newJs = moduleEntry.concat(jsD)
    }
    if (!moduleEntry) newJs = jsD

    const uncheckedSku = moduleSummaryNew[idx]
    if (!uncheckedSku.checked) {
        newJs = newJs.filter(data => { return data.skuUuid !== uncheckedSku.skuUuid })
    }
    preAddHandler(newJs);
}










//remove sku
export const removeSku = async ({ dispatch, moduleEntry, moduleName }) => {
    let jobSheet = { ...moduleEntry }
    const list = moduleEntry.jobPost.list.filter(data => { if (data.checked) return data })
    jobSheet.jobPost.list = list
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: jobSheet })
    if (list.length === 0) removeEntryModule({ id: jobSheet.jobPost.id, moduleName, dispatch })
}





export const submitValidation = ({ moduleEntry, moduleName, setConfirmation, dispatch, setIsRequired }) => {
    let isClear = []
    let js = { ...moduleEntry }
    const jsR = moduleEntry.jobPost.list.map(data => {
        data.poList.map(po => {
            const qtyCount = totalListItemWeightCount({moduleList:po.itemList})
            po.itemList.map(item => {
                const { qty, grade, ripeness, reason, pack } = item
                isNaN(qty) || qty == 0 || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                !item?.codeOutbound?.value ? item.outboundTypeIsEmpty = true : item.outboundTypeIsEmpty = false

                //outbound type lainnya tidak boleh kosong
                if (item?.codeOutbound?.value == 'lainnya') {
                    item?.note == '' || item?.note === undefined ? item.noteIsEmpty = true : item.noteIsEmpty = false
                } else {
                    item.noteIsEmpty = false
                }
                if (js.jobPost.areaName?.toLowerCase() === 'staging' && (data?.category === 'Fruits' || data?.category === 'Vegetables' ? true : false)) {
                    if (!pack) item.packIsEmpty = true
                }

                if(!item.qtyIsEmpty){
                    if(qtyCount > po.initialWeight){
                        item.qtyIsEmpty = true
                    }
                    else item.qtyIsEmpty = false
                }

                // if(!item.qtyIsEmpty) po.initialWeightOver > po.initialWeight ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                if (item?.qtyIsEmpty || item?.outboundTypeIsEmpty || item?.noteIsEmpty || item?.packIsEmpty) isClear.push(false)
                else isClear.push(true)

                return item
            })
            return po
        })
        return data
    })


    //validate there is value
    let valid = false
    jsR.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                let i = false
                if (item.qty) valid = true
            })
        })
    })

    js.jobPost.list = jsR
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
    if (!isClear.includes(false) && valid) {
        setConfirmation({ show: true, name: 'submit' })
    }
    else {
        setConfirmation({ show: false, name: null })
        setIsRequired(true)
    }
}