export const onClose = ({ setShow, parentOncCloseFunction }) => {
  setShow(false);
  console.log(parentOncCloseFunction)
  if (parentOncCloseFunction) {
    parentOncCloseFunction();
  }

};

export const refreshPage = ({ refresh = true }) => {
  if (refresh === true) {
    window.location.reload(false);
  }
};
